.App {
    text-align: center;
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 80px;
}

.App-header {
    background-color: #222;
    height: 150px;
    padding: 20px;
    color: white;
}

.App-title {
    font-size: 1.5em;
}

.App-intro {
    font-size: large;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.body {
    padding: 0;
    margin: 0;
}

.Title {
    height: 2rem;
    line-height: 2rem;
    margin: 0;
    background-color: lightblue;
}

.Page {
    height: 10rem;
    width: 100%;
    background-color: white;
}

.Contents p {
    margin: 0;
}

.Pagenation {
    position: absolute;
    bottom: 0px;
    float: left;
    width: 100%;
    height: 3rem;
    background-color: lightgray;
}

.prevPage {
    float: left;
    width: 46%;
    height: 2rem;
    line-height: 2rem;
    margin: 0.5% 1% 2% 2%;
    background-color: lightgreen;
}

.nextPage {
    float: left;
    width: 46%;
    height: 2rem;
    line-height: 2rem;
    margin: 0.5% 2% 2% 1%;
    background-color: lightcoral;
}

.QAIndicator {
    height: 1rem;
    background-color: yellow;
    line-height: 1rem;
    font-size: 0.8rem;
}



